h1 {
  font-size: 7.125rem;
}

h2 {
  font-size: 6rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 1.125rem;
}

h6 {
  font-size: 1rem;
}

@font-face {
  font-family: AGaramondPro-Italic;
  src: url("../../fonts/AGaramondPro-Italic.ttf") format("truetype"), url("../../fonts/AGaramondPro-Italic.woff") format("woff"), url("../../fonts/AGaramondPro-Italic.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: AGaramondPro-Regular;
  src: url("../../fonts/AGaramondPro-Regular.ttf") format("truetype"), url("../../fonts/AGaramondPro-Regular.woff") format("woff"), url("../../fonts/AGaramondPro-Regular.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: Arial-Regular;
  src: url("../../fonts/Arial-Regular.ttf") format("truetype"), url("../../fonts/Arial-Regular.woff") format("woff"), url("../../fonts/Arial-Regular.woff2") format("woff2");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: 'icomoon';
  src: url("../../fonts/icomoon.eot?gdhs90");
  src: url("../../fonts/icomoon.eot?gdhs90#iefix") format("embedded-opentype"), url("../../fonts/icomoon.ttf?gdhs90") format("truetype"), url("../../fonts/icomoon.woff?gdhs90") format("woff"), url("../../fonts/icomoon.svg?gdhs90#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-plus1:before {
  content: "\e901";
}

.icon-arrow-right:before {
  content: "\e900";
}

@keyframes defaultParties {
  to {
    transform: translateX(0);
  }
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

html,
body {
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "AGaramondPro-Regular", sans-serif;
  color: #000;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

input,
button,
textarea,
select {
  border: none;
}

button {
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

textarea {
  resize: none;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  display: inline-block;
  text-decoration: none;
  cursor: pointer;
}

.wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100vh;
  overflow-x: hidden;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container-fluid {
  max-width: 1800px;
  padding: 0 15px;
  margin: 0 auto;
}

.container {
  max-width: 1460px;
  padding: 0 15px;
  margin: 0 auto;
}

.img {
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
}

.coll-number {
  color: #a9a9a9;
  line-height: 30px;
  font-size: 0.875rem;
  display: inline-block;
  width: 60px;
}

.row {
  display: flex;
}

.row:first-child {
  margin-bottom: 2.5rem;
}

.row:last-child {
  align-items: flex-end;
}

.model-page::before, .model-page::after {
  content: "";
  position: absolute;
  z-index: -1;
  height: 0;
  width: 0;
  transition: all 3s ease;
}

.model-page::before {
  background-color: #e5eaeb;
}

.model-page::after {
  background-color: #ede0df;
}

.model-page--vertical::before, .model-page--vertical::after {
  top: 0;
  height: 100%;
  width: 50%;
  animation: defaultParties 1s ease-in forwards;
}

.model-page--vertical::before {
  right: 0;
  transform: translateX(100%);
}

.model-page--vertical::after {
  left: 0;
  transform: translateX(-100%);
}

.model-page--horizontal::before, .model-page--horizontal::after {
  left: 0;
  height: 50%;
  width: 100%;
  transition: all 3s ease;
}

.model-page--horizontal::before {
  bottom: 0;
}

.model-page--horizontal::after {
  top: 0;
}

.model-page--horizontal-mode-1::before, .model-page--horizontal-mode-1::after {
  left: 0;
  width: 100%;
}

.model-page--horizontal-mode-1::before {
  bottom: 0;
  height: 80%;
}

.model-page--horizontal-mode-1::after {
  top: 0;
  height: 20%;
}

.model-page--horizontal-mode-2::before, .model-page--horizontal-mode-2::after {
  left: 0;
  width: 100%;
}

.model-page--horizontal-mode-2::before {
  bottom: 0;
  height: 20%;
}

.model-page--horizontal-mode-2::after {
  top: 0;
  height: 80%;
}

.model-socials li {
  display: inline-block;
}

.model-socials li + li {
  margin-left: 15px;
}

.model-socials li a {
  color: #000;
  padding: 3px;
  font-size: 18px;
  position: relative;
}

.model-socials li a::before {
  content: "";
  position: absolute;
  bottom: 3px;
  right: 0;
  height: 1px;
  width: 0;
  background-color: #000;
  transition: all 0.3s ease-out;
}

.model-socials li a:hover::before {
  left: 0;
  width: 100%;
}

.header {
  padding-top: 1.25rem;
  text-align: center;
}

.header__logo svg {
  display: block;
  margin: 5px auto;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 50%;
  width: 100%;
  margin-left: auto;
}

.footer-collection__title {
  position: relative;
  line-height: 48px;
  font-size: 3rem;
  padding-right: 125px;
}

.footer-collection__title::before {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  height: 1px;
  width: 110px;
  background-color: #000;
}

.see-link {
  font-size: 1.5rem;
  color: #a9a9a9;
  transform: translateX(-50%);
  transition: all 3s ease-in;
  position: relative;
}

.see-link--close::before {
  content: '';
  position: absolute;
  top: 45%;
  left: 0;
  width: 110%;
  height: 2px;
  background-color: #a9a9a9;
  transition: all 3s ease-in-out;
}

.see-link--open::before {
  content: '';
  position: absolute;
  top: 45%;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #a9a9a9;
  transition: all 3s ease-in-out;
}

.main-collections {
  padding: 2.5rem 0;
  position: relative;
}

.main-collections::before {
  content: 'Catalogue';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-size: 24.1875rem;
  color: #ede0df;
  display: flex;
  align-items: center;
  justify-content: center;
}

.model-item {
  display: block;
  max-width: 285px;
  width: 100%;
  padding-left: 45px;
  position: relative;
}

.model-item::before {
  content: '\e900';
  position: absolute;
  font-family: 'icomoon';
  bottom: -12%;
  left: 0;
  color: #000;
  font-size: 1.375rem;
}

.model-item + .model-item {
  margin-left: 135px;
}

.model-item__image {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 300px;
  position: relative;
  height: 100%;
}

.model-item__image::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
}

.model-item__number {
  position: absolute;
  top: 50px;
  left: 0;
  height: 0;
  width: 0;
  transform: rotate(-90deg);
  white-space: nowrap;
}

.model-item__title {
  position: absolute;
  bottom: 4%;
  left: 0;
  text-transform: capitalize;
  color: #000;
  letter-spacing: -0.64px;
}

.model-list--left {
  margin-left: 10.9375rem;
}

.model-list--right {
  margin-right: auto;
}

.model-list li {
  margin-bottom: 0.9375rem;
}

.model-list li:last-child {
  margin-bottom: 0;
}

.model-list a {
  position: relative;
  width: 100%;
  font-size: 0.875rem;
  padding-right: 6.25rem;
}

.model-list a::before {
  font-family: 'icomoon';
  font-size: 1.57143rem;
  position: absolute;
  top: 0;
  right: 0;
  content: '\e900';
  color: #a9a9a9;
}

.model-list__number {
  letter-spacing: -.4px;
  margin-right: 4.375rem;
}

.model-list__title {
  color: #000;
  letter-spacing: -.4px;
}

.current-model__slider {
  height: calc(100vh - 130px);
}

.current-model__inner {
  position: relative;
  padding-right: 70px;
}

.current-model-item {
  overflow: hidden;
}

.current-model__arrow {
  position: absolute;
  top: -45px;
  font-size: 1.5rem;
  color: #a9a9a9;
  z-index: 10;
  text-transform: lowercase;
  outline: none;
}

.current-model__arrow::before {
  content: "";
  position: absolute;
  top: 50%;
  height: 1px;
  width: 72px;
  background-color: #a9a9a9;
}

.current-model__arrow-prev {
  padding-left: 80px;
  left: 0;
}

.current-model__arrow-prev::before {
  left: 0;
}

.current-model__arrow-next {
  padding-right: 80px;
  right: 0;
}

.current-model__arrow-next::before {
  right: 0;
}

.current-model__pagination {
  position: absolute;
  right: 0;
  top: 50%;
  display: flex;
  flex-direction: column;
  z-index: 10;
  transform: translateY(-50%);
}

.current-model__pagination-dot {
  text-align: center;
  cursor: pointer;
  outline: none;
  position: realtive;
  padding: 10px 0;
  transition: font-size 0.3s ease-in;
}

.current-model__pagination-dot::before, .current-model__pagination-dot::after {
  content: "";
  height: 0;
  transition: all 0.5s ease-in;
}

.current-model__pagination-dot + .current-model__pagination-dot {
  margin-top: 20px;
}

.current-model__pagination-dot.current-model__pagination-dot--active {
  margin-top: 90px;
  margin-bottom: 70px;
}

.current-model__pagination-dot:first-child {
  margin-top: 0;
}

.current-model__pagination-dot:first-child::before {
  display: none;
}

.current-model__pagination-dot:last-child {
  margin-bottom: 0;
}

.current-model__pagination-dot:last-child::after {
  display: none;
}

.current-model__pagination-dot:first-child.current-model__pagination-dot--active {
  margin-bottom: 70px;
}

.current-model__pagination-dot:last-child.current-model__pagination-dot--active {
  margin-top: 90px;
}

.current-model__pagination-dot--active {
  position: relative;
  font-size: 3rem;
}

.current-model__pagination-dot--active::before, .current-model__pagination-dot--active::after {
  content: "";
  position: absolute;
  left: 50%;
  height: 6.25rem;
  width: 1px;
  background-color: #000;
}

.current-model__pagination-dot--active::before {
  bottom: 65px;
}

.current-model__pagination-dot--active::after {
  top: 60px;
}

.acquaintance-model {
  display: flex;
}

.acquaintance-model__content {
  padding: 3.125rem 0;
  flex: 0 1 60%;
}

.acquaintance-model__images {
  padding: 6.25rem 0 0;
  flex: 1 1 40%;
  text-align: center;
  position: relative;
}

.acquaintance-model__images .img {
  position: absolute;
  bottom: 60px;
  left: -150px;
  width: 125%;
  min-height: 430px;
}

.acquaintance-model__headline {
  position: relative;
  display: inline-block;
}

.acquaintance-model__headline a {
  position: absolute;
  bottom: 0;
  right: 20px;
  font-size: 1.25rem;
  color: #000;
}

.acquaintance-model__about {
  margin-top: 7.1875rem;
}

.acquaintance-model__about-list li {
  font-size: 1rem;
  position: relative;
}

.acquaintance-model__about-list li span {
  color: #5d5d5d;
}

.acquaintance-model__about-list li + li {
  margin-top: 5px;
}

.acquaintance-model__about-list li:first-child {
  padding-left: 110px;
}

.acquaintance-model__about-list li:first-child::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  height: 1px;
  width: 100px;
  background-color: #000;
}

.acquaintance-model__about-list li:nth-child(2) {
  padding-left: 95px;
}

.acquaintance-model__about-list li:nth-child(2)::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  height: 1px;
  width: 85px;
  background-color: #000;
}

.acquaintance-model__about-list li:nth-child(3) {
  padding-left: 70px;
}

.acquaintance-model__about-list li:nth-child(3)::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  height: 1px;
  width: 60px;
  background-color: #000;
}

.acquaintance-model__about-list li:nth-child(4) {
  padding-left: 100px;
}

.acquaintance-model__about-list li:nth-child(4)::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  height: 1px;
  width: 90px;
  background-color: #000;
}

.acquaintance-model__about-list li:nth-child(5) {
  padding-left: 130px;
}

.acquaintance-model__about-list li:nth-child(5)::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  height: 1px;
  width: 120px;
  background-color: #000;
}

.acquaintance-model__about-list li:nth-child(6) {
  padding-left: 60px;
}

.acquaintance-model__about-list li:nth-child(6)::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  height: 1px;
  width: 50px;
  background-color: #000;
}

.acquaintance-model__about-descr {
  max-width: 500px;
  padding-top: 0.625rem;
  margin: 3.125rem 170px 0 auto;
  font-size: 1.25rem;
  position: relative;
}

.acquaintance-model__about-descr::before {
  content: "";
  position: absolute;
  top: 0;
  left: -50px;
  height: 1px;
  width: 100%;
  background-color: #000;
}

.acquaintance-model__socials {
  display: inline-block;
  margin-top: 12%;
}

.best-photo-model {
  padding: 3.125rem 0 0;
}

.best-photo-model__headline {
  max-width: 600px;
  width: 100%;
}

.best-photo-model__headline-title {
  white-space: nowrap;
  margin-bottom: 10px;
}

.best-photo-model__headline-descr {
  height: 150px;
}

.best-photo-model__gallery {
  position: relative;
}

.best-photo-model__gallery .img:first-child {
  position: absolute;
  right: 0;
  top: -160px;
  width: 100%;
  max-width: 300px;
  min-height: 400px;
}

.best-photo-model__gallery .img:nth-child(2) {
  position: absolute;
  right: 20%;
  top: -120px;
  width: 100%;
  max-width: 300px;
  min-height: 450px;
}

.best-photo-model__gallery .img:nth-child(3) {
  position: absolute;
  left: 24%;
  top: 0;
  width: 100%;
  max-width: 500px;
  min-height: 300px;
}

.best-photo-model__gallery .img:nth-child(4) {
  position: absolute;
  left: 0;
  top: 10px;
  width: 100%;
  max-width: 400px;
  min-height: 350px;
}

.portfolio-model {
  padding: 3.125rem 0 0;
}

.portfolio-model__gallery {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.portfolio-model__gallery .img {
  flex: 0 1 calc(33.333333% - 15px);
  min-height: 250px;
  margin-bottom: 20px;
  cursor: pointer;
}

.portfolio-model__gallery .img:nth-child(2) {
  background-position: center center;
}

.end-model {
  padding: 3.125rem 0 0;
}

.end-model__headline {
  margin-bottom: 10px;
}

.end-model__headline-descr {
  max-width: 400px;
  height: 90px;
  overflow: hidden;
}

.end-model__inner {
  display: flex;
  justify-content: space-between;
}

.end-model__item {
  flex: 1 1 33.3333%;
}

.end-model__item + .end-model__item {
  margin-left: 50px;
}

.end-model__images {
  flex: 0 1 20%;
}

.end-model__images .img {
  min-height: 450px;
}

.end-model__socials {
  margin: 0 0 6% 5%;
  align-self: flex-end;
}

.end-model__contacts-title {
  margin-bottom: 1.25rem;
}

.end-model__contacts-list {
  margin-bottom: 10px;
}

.end-model__contacts-list li + li {
  margin-top: 10px;
}

.end-model__contacts a {
  color: #000;
}

@media (max-height: 790px) {
  .model-item {
    max-width: 240px;
  }
  .model-item__image {
    min-height: 240px;
  }
  .model-item__title {
    bottom: 6%;
  }
  .footer {
    margin-bottom: 0;
  }
}

@media (max-height: 730px) {
  .header {
    padding-top: 0.375rem;
  }
  .main-collections {
    padding: 1.25rem 0;
  }
  .model-item {
    max-width: 190px;
  }
  .model-item__image {
    min-height: 200px;
  }
  .model-item__title {
    font-size: 1.125rem;
  }
  .model-list li {
    margin-bottom: 0.3125rem;
  }
}
