.model-item {
  display: block;
  max-width: 285px;
  width: 100%;
  padding-left: 45px;
  position: relative;
  &::before {
    content: '\e900';
    position: absolute;
    font-family: 'icomoon';
    bottom: -12%;
    left: 0;
    color: $main-color;
    font-size: rem(22px);
  }
  & + .model-item {
    margin-left: 135px;
  }
  
  &__image {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 300px;
    position: relative;
    height: 100%;
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, .2);
    }
  } 
  &__number {
    position: absolute;
    top: 50px;
    left: 0;
    height: 0;
    width: 0;
    transform: rotate(-90deg);
    white-space: nowrap;
  }
  &__title {
    position: absolute;
    bottom: 4%;
    left: 0;
    text-transform: capitalize;
    color: $black;
    letter-spacing: -0.64px;
   
  }
}