@mixin icomoon($path, $id) {
  @font-face {
    font-family: 'icomoon';
    src:  url('#{$path}/icomoon.eot?#{$id}');
    src:  url('#{$path}/icomoon.eot?#{$id}#iefix') format('embedded-opentype'),
      url('#{$path}/icomoon.ttf?#{$id}') format('truetype'),
      url('#{$path}/icomoon.woff?#{$id}') format('woff'),
      url('#{$path}/icomoon.svg?#{$id}#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
  
  [class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }  
}

@mixin font-face($path, $name) {
  @font-face { 
    font-family: #{$name};

    src: url("#{$path}/#{$name}.ttf") format("truetype"),
      url("#{$path}/#{$name}.woff") format("woff"),
      url("#{$path}/#{$name}.woff2") format("woff2");

    font-style: normal; 
    font-weight: normal;
    font-display: swap; 
    } 
} 

